'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';
import {
  Calendar,
  Clock,
  DollarSign,
  Users,
  TrendingUp,
  AlertCircle,
  CheckCircle,
  XCircle,
} from 'lucide-react';

type Stats = {
  totalReservations: number;
  pendingReservations: number;
  confirmedReservations: number;
  totalRevenue: number;
  recentReservations: any[];
};

export default function AdminDashboard() {
  const [stats, setStats] = useState<Stats>({
    totalReservations: 0,
    pendingReservations: 0,
    confirmedReservations: 0,
    totalRevenue: 0,
    recentReservations: [],
  });
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Aquí conectaremos con las APIs reales
    // Por ahora, datos de ejemplo
    setStats({
      totalReservations: 45,
      pendingReservations: 12,
      confirmedReservations: 33,
      totalRevenue: 3250,
      recentReservations: [
        {
          id: 1,
          nombre: 'Juan Pérez',
          tipo_misa: 'simple',
          fecha: '2025-12-20',
          status: 'pending',
        },
        {
          id: 2,
          nombre: 'María García',
          tipo_misa: 'novenario',
          fecha: '2025-12-21',
          status: 'confirmed',
        },
      ],
    });
    setIsLoading(false);
  }, []);

  const statCards = [
    {
      name: 'Total Reservas',
      value: stats.totalReservations,
      icon: Calendar,
      color: 'bg-blue-500',
      change: '+12%',
      changeType: 'increase',
    },
    {
      name: 'Pendientes',
      value: stats.pendingReservations,
      icon: Clock,
      color: 'bg-yellow-500',
      change: '-3%',
      changeType: 'decrease',
    },
    {
      name: 'Confirmadas',
      value: stats.confirmedReservations,
      icon: CheckCircle,
      color: 'bg-green-500',
      change: '+8%',
      changeType: 'increase',
    },
    {
      name: 'Ingresos',
      value: `S/. ${stats.totalRevenue.toLocaleString()}`,
      icon: DollarSign,
      color: 'bg-purple-500',
      change: '+15%',
      changeType: 'increase',
    },
  ];

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Cargando dashboard...</p>
        </div>
      </div>
    );
  }

  return (
    <div>
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
        <p className="text-gray-600 mt-1">
          Bienvenido al panel de administración de la Parroquia
        </p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        {statCards.map((stat) => {
          const Icon = stat.icon;
          return (
            <div
              key={stat.name}
              className="bg-white rounded-lg shadow-sm p-6 hover:shadow-md transition-shadow"
            >
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">
                    {stat.name}
                  </p>
                  <p className="text-2xl font-bold text-gray-900 mt-2">
                    {stat.value}
                  </p>
                </div>
                <div className={`${stat.color} p-3 rounded-lg`}>
                  <Icon className="w-6 h-6 text-white" />
                </div>
              </div>
              <div className="mt-4 flex items-center text-sm">
                <TrendingUp
                  className={`w-4 h-4 mr-1 ${
                    stat.changeType === 'increase'
                      ? 'text-green-600'
                      : 'text-red-600'
                  }`}
                />
                <span
                  className={
                    stat.changeType === 'increase'
                      ? 'text-green-600'
                      : 'text-red-600'
                  }
                >
                  {stat.change}
                </span>
                <span className="text-gray-600 ml-2">vs. mes anterior</span>
              </div>
            </div>
          );
        })}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Recent Reservations */}
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-xl font-bold text-gray-900">
              Reservas Recientes
            </h2>
            <Link
              href="/admin/reservations"
              className="text-sm text-blue-600 hover:text-blue-800"
            >
              Ver todas →
            </Link>
          </div>

          <div className="space-y-4">
            {stats.recentReservations.length > 0 ? (
              stats.recentReservations.map((reservation) => (
                <div
                  key={reservation.id}
                  className="flex items-center justify-between p-4 bg-gray-50 rounded-lg"
                >
                  <div>
                    <p className="font-medium text-gray-900">
                      {reservation.nombre}
                    </p>
                    <p className="text-sm text-gray-600">
                      {reservation.tipo_misa} - {reservation.fecha}
                    </p>
                  </div>
                  <div>
                    {reservation.status === 'pending' ? (
                      <span className="px-3 py-1 bg-yellow-100 text-yellow-800 text-xs font-medium rounded-full">
                        Pendiente
                      </span>
                    ) : (
                      <span className="px-3 py-1 bg-green-100 text-green-800 text-xs font-medium rounded-full">
                        Confirmada
                      </span>
                    )}
                  </div>
                </div>
              ))
            ) : (
              <p className="text-gray-500 text-center py-8">
                No hay reservas recientes
              </p>
            )}
          </div>
        </div>

        {/* Quick Actions */}
        <div className="bg-white rounded-lg shadow-sm p-6">
          <h2 className="text-xl font-bold text-gray-900 mb-4">
            Acciones Rápidas
          </h2>
          <div className="grid grid-cols-2 gap-4">
            <Link
              href="/admin/reservations"
              className="p-4 bg-blue-50 hover:bg-blue-100 rounded-lg transition-colors text-center"
            >
              <Calendar className="w-8 h-8 text-blue-600 mx-auto mb-2" />
              <p className="text-sm font-medium text-gray-900">Ver Reservas</p>
            </Link>
            <Link
              href="/admin/mass-times"
              className="p-4 bg-green-50 hover:bg-green-100 rounded-lg transition-colors text-center"
            >
              <Clock className="w-8 h-8 text-green-600 mx-auto mb-2" />
              <p className="text-sm font-medium text-gray-900">Horarios</p>
            </Link>
            <Link
              href="/admin/gallery"
              className="p-4 bg-purple-50 hover:bg-purple-100 rounded-lg transition-colors text-center"
            >
              <Users className="w-8 h-8 text-purple-600 mx-auto mb-2" />
              <p className="text-sm font-medium text-gray-900">Galería</p>
            </Link>
            <Link
              href="/admin/settings"
              className="p-4 bg-gray-50 hover:bg-gray-100 rounded-lg transition-colors text-center"
            >
              <AlertCircle className="w-8 h-8 text-gray-600 mx-auto mb-2" />
              <p className="text-sm font-medium text-gray-900">
                Configuración
              </p>
            </Link>
          </div>
        </div>
      </div>

      {/* Alerts */}
      <div className="mt-6 grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="bg-yellow-50 border-l-4 border-yellow-400 p-4 rounded">
          <div className="flex">
            <AlertCircle className="w-5 h-5 text-yellow-400" />
            <div className="ml-3">
              <h3 className="text-sm font-medium text-yellow-800">
                Reservas pendientes de confirmar
              </h3>
              <p className="text-sm text-yellow-700 mt-1">
                Tienes {stats.pendingReservations} reservas esperando
                confirmación de pago.
              </p>
              <Link
                href="/admin/reservations?status=pending"
                className="text-sm font-medium text-yellow-800 hover:text-yellow-900 mt-2 inline-block"
              >
                Revisar ahora →
              </Link>
            </div>
          </div>
        </div>

        <div className="bg-green-50 border-l-4 border-green-400 p-4 rounded">
          <div className="flex">
            <CheckCircle className="w-5 h-5 text-green-400" />
            <div className="ml-3">
              <h3 className="text-sm font-medium text-green-800">
                Sistema funcionando correctamente
              </h3>
              <p className="text-sm text-green-700 mt-1">
                Todas las APIs están operativas y la base de datos está
                conectada.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
